$light-blue: #03b7e4;
$dark-purple: #1b0618;
$light-color: #edeade;
$dark-blue: #011521;

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
ol,
a {
  padding: 0;
  margin: 0;
  text-decoration: none;
}
// Font Family
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
span,
button {
  font-family: "Times New Roman", Times, serif;
}

// FONT SIZES
$min-size: 14;
$max-size: 50;

@for $i from $min-size through $max-size {
  .size-#{$i} {
    font-size: #{$i}px;

    @media (max-width: 425px) {
      font-size: #{max($i - 5, 10)}px;
    }
  }
}
// ROW GAPS
$row-gaps: (
  10: 10px,
  20: 20px,
  30: 30px,
  40: 40px,
  50: 50px,
  60: 60px,
  70: 70px,
  100: 100px,
);

@each $key, $gap in $row-gaps {
  .rowGap-#{$key} {
    display: flex;
    flex-direction: column;
    row-gap: $gap;

    @if $key == 100 {
      @media (max-width: 768px) {
        row-gap: 50px;
      }
    }
  }
}

// CONTAINER
$breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1320px,
);

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;

  @each $breakpoint, $width in $container-max-widths {
    @media (min-width: map-get($breakpoints, $breakpoint)) {
      max-width: $width;
    }
  }
}

// TITLES
$title-sizes: (
  lg: 30px,
  md: 25px,
  sm: 20px,
  xs: 16px,
);

.title {
  font-family: "Times New Roman", Times, serif;
  font-size: 42px;
  color: #000;
  @media (max-width: 425px) {
    font-size: 35px;
  }
  @each $size, $value in $title-sizes {
    &-#{$size} {
      color: $light-blue;
      font-size: $value;
    }
  }
}

// PARAGRAPH
.paragraph-black {
  color: #000000;
}

.fw-normal {
  font-weight: normal;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.bluredBlock {
  background-color: rgba(26, 32, 44, 0.1);
  color: white;
  font-weight: bold;
  border-radius: 20px;
  padding: 20px;
  backdrop-filter: blur(5px);
}
.roundedBlock {
  background-color: #ececec;
  border-radius: 35px;
  padding: 30px;
}
.logoBackSide {
  background-color: rgba(253, 245, 230, 0.8);
  border-radius: 10px;
  padding: 5px 15px;
  backdrop-filter: blur(5px);
  width: fit-content;
}

.fitContent {
  width: fit-content;
}

.alignCenter {
  align-items: center;
}

.headerContent {
  display: flex;
  align-items: center;
  flex-direction: column;
  row-gap: 10px;
}

.mt-15 {
  margin-top: 15px;
}
.mt-30 {
  margin-top: 30px;
}
.mt-45 {
  margin-top: 45px;
}

.paragraph-dark {
  color: $dark-purple;
}

.text-center {
  text-align: center;
}

.hamburger-react {
  display: none;
  z-index: 22;
  @media (max-width: 768px) {
    display: block;
  }
}

.flexData {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

input,
textarea {
  border: 1px solid #00326d;
  padding: 15px;
  border-radius: 10px;
  background-color: transparent;
  font-size: small;
  font-weight: bold;
  text-align: center;
  color: #00326d;
  transition: box-shadow 0.3s ease-in-out, transform 0.2s ease-in-out;
  &::placeholder {
    color: #00326d;
  }
  &:focus {
    outline-color: #00326d;
    color: #00326d;
    box-shadow: 5px 5px #888888;
    box-shadow: 8px 8px 15px rgba(136, 136, 136, 0.6);
    transform: translateY(-3px);
  }
}
textarea {
  transition: 0.4s;
  resize: vertical;
}

.btn {
  max-width: 200px;
  width: 100%;
  background: none;
  padding: 1.5em 1em;
  text-transform: uppercase;
  letter-spacing: 2.2px;
  cursor: pointer;
  border-radius: 10px;
  transform: translateY(0);
  transition: all 0.5s linear;
  &:hover {
    transform: translateY(-0.5rem);
  }
}

.btn-light {
  border: 2px solid $light-color;
  color: $light-color;
}
.btn-light:hover {
  box-shadow: 0 0 1.1rem 8px $light-color;
}

.btn-dark {
  border: 2px solid #028fb2;
  color: #028fb2;
}
.btn-dark:hover {
  box-shadow: 0 0 1.1rem 8px #028fb2;
}
