@import "index";

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.content {
  flex: 1;
}

// Header
header {
  background: #ffffff;
  padding: 20px 0;
  .mainHeaderContent {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 20px;
    .logoBlock {
      img {
        cursor: pointer;
        object-fit: cover;
        max-width: 200px;
        width: 100%;
        @media (max-width: 425px) {
          max-width: 110px;
        }
      }
    }
    .menuLinks {
      display: flex;
      align-items: center;
      column-gap: 25px;
      h4 {
        color: $dark-blue;
        cursor: pointer;
        transition: 0.5s;
        transform: translateZ(0);
        backface-visibility: hidden;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-family: "Times New Roman", Times, serif;
        &.active {
          color: $light-blue;
        }
        &:hover {
          transform: rotate(-10deg);
        }
      }
    }
    .desktopMenu {
      @media (max-width: 768px) {
        display: none;
      }
    }
    .mobileMenu {
      display: none;
      z-index: 22;
      flex-direction: column;
      justify-content: center;
      background: $dark-blue;
      transform: translateX(-100%);
      height: 100vh;
      position: absolute;
      top: 0;
      left: 0;
      transition: transform 0.4s ease-in-out;
      h4 {
        font-size: 30px;
        color: #fff;
      }
      &.open {
        transform: translateX(0);
      }
      @media (max-width: 768px) {
        display: flex;
        width: 100%;
      }
    }
  }
}

// Footer
.footerMain {
  color: #90949c;
  background: #011521;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 30px 20px;
  font-family: "Times New Roman", Times, serif;
}

.footerContainer {
  display: flex;
  max-width: 1200px;
  width: -webkit-fill-available;
  justify-content: space-around;
  align-items: center;
  column-gap: 10px;

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: unset;
    width: auto;
  }
}

.footerWidgets {
  display: flex;
  justify-content: center;
  gap: 10rem;
}

.footerWidgetWrapper {
  margin-bottom: 30px;
}

.footerWidgetTitle {
  margin-bottom: 20px;
  display: flex;
  max-width: 350px;
  > h2 {
    color: #fff;
    font-size: 24px;
    padding-bottom: 18px;
    position: relative;
    font-family: "Times New Roman", Times, serif;

  }
  > h2:before {
    background: $light-blue;
    bottom: 0;
    content: "";
    height: 4px;
    left: 0;
    position: absolute;
    width: 34px;
  }
}

.footerReachOut {
  @media (max-width: 768px) {
    font-size: 14px;
  }
  @media (max-width: 480px) {
    font-size: 12px;
  }
}

.footerContactLink {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  > ul {
    > li {
      gap: 1rem;
      display: flex;
      color: #90949c;
      > i {
        margin-bottom: 4px;
      }
      > span {
        > a {
          color: #90949c;
          width: 77%;
          font-weight: 600;
          @media (max-width: 768px) {
            font-size: 14px;
          }
          @media (max-width: 480px) {
            font-size: 12px;
          }
        }
        > a:hover {
          color: $light-blue;
        }
      }
    }
  }
}

.footerCopyrightWrapper {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
}

.footerCopyrightText {
  width: fit-content;
  margin: auto;
  > span {
    color: $light-blue;
  }
  @media (max-width: 480px) {
    font-size: 3.5vw;
  }
}

.footerSocialWrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  width: max-content;
  @media (max-width: 768px) {
    flex-direction: row;
  }
  @media (max-width: 1024px) {
    width: fit-content;
  }
}

.footerSocialWrapper a {
  width: 40px;
  height: 40px;
  background-color: #fff;
  justify-content: center;
  display: flex;
  align-items: center;
  font-size: 18px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid #fff;
  z-index: 1;
  text-decoration: none;
}

.footerSocialWrapper a .icon {
  position: relative;
  color: #262626;
  transition: 0.5s;
  z-index: 3;
}

.footerSocialWrapper a:hover .icon {
  color: #fff;
  transform: rotateY(360deg);
}

.footerSocialWrapper a:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.7s;
  z-index: 2;
}

.footerSocialWrapper a:hover:before {
  top: 0;
}

.footerSocialWrapper a.facebook:before {
  background: #4267b2;
}

.footerSocialWrapper a.twitter:before {
  background: #1da1f2;
}

.footerSocialWrapper a.instagram:before {
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
}

.footerSocialWrapper a.linkedin:before {
  background: #0e76a8;
}
.footerSocialWrapper a.youtube:before {
  background: #ff0000;
}
.footerSocialWrapper a.twitch:before {
  background: #6441a5;
}
